body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  text-align: left;
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  line-height: 14px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  margin-top: 0;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.cardprice-value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.cardprice-header {
  text-align: left;
  color: #616070;
  font-size: 12px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin-top: 0;
  margin-bottom: 24px;
  display: flex;
}

.cardreview-count {
  text-align: right;
  color: #060b35;
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__btn:hover {
  color: #00acdc;
  background: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(n+1) {
  background-image: url("star-active.edda4c43.svg");
}
